/**
 * Hilfsfunktion: Fhre einen visuellen Fokus-Effekt auf einem Container aus.
 * @param {HTMLElement} container - Der Container, der den Fokus erhlt.
 * @param {string} focusClass - CSS-Klasse fr den spezifischen Fokus-Ring (z.B. 'focus-ring-secondary').
 */
export function applyFocusEffect(container, focusClass) {
    const toggle = container.querySelector("[data-target='offdrop']");
    // Wende Klassen mit Verzgerung an und entferne sie spter wieder.
    setTimeout(() => {
        toggle?.classList.add('focus', 'focus-ring', focusClass);
        toggle && (toggle.style.borderColor = 'var(--bs-secondary)');
    }, 300);
    setTimeout(() => {
        toggle?.classList.remove('focus', 'focus-ring', focusClass);
        toggle && (toggle.style.borderColor = '');
    }, 3300);
}

/**
 * Hilfsfunktion: Automatisches Auswhlen des ersten sichtbaren, gltigen Inputs.
 * @param {HTMLInputElement[]} inputs - Array von Ziel-Inputs.
 * @param {function(HTMLElement): void} [focusCallback] - Optional: Callback fr Fokus-Effekt auf den Container.
 * @param {boolean} [debug=false] - Optional: Wenn true, werden Debug-Logs ausgegeben.
 */
export const autoSelect = (inputs, focusCallback, debug = false) => {
    const log = (...args) => debug && console.log('[autoSelect]', ...args);
    const warn = (...args) => debug && console.warn('[autoSelect]', ...args);

    log('autoSelect inputs:', inputs);

    // 1) Nur sichtbare und nicht-leere Inputs
    const candidates = inputs.filter(input => {
        const group = input.closest('.btn, .btn-group, .values-group');
        const visible = group && !group.classList.contains('d-none');
        const nonEmpty = input.value.trim() !== '';
        return visible && nonEmpty;
    });
    log('Auto-select candidates:', candidates);

    // 2) Falls bereits eine Auswahl besteht, nichts tun
    if (candidates.some(input => input.checked)) {
        log('Already selected, aborting.');
        return;
    }

    // 3) Ersten Kandidaten whlen und Event auslsen
    const first = candidates[0];
    if (first) {
        first.checked = true;
        first.dispatchEvent(new Event('change', { bubbles: true }));
        log('Auto-select:', first.value);
    } else {
        warn('No valid candidates found for auto-select.');
    }
};


/**
 * Dependencies: Values  Values
 * Steuerung von Ziel-Inputs basierend auf dem Wert eines Trigger-Inputs.
 * @param {string} triggerName - Name der Trigger-Input-Gruppe.
 * @param {function(string): boolean} condition - Funktion, die bei einem Trigger-Wert true/false liefert.
 * @param {string} targetOptionName - Name der Ziel-Input-Gruppe.
 * @param {function(string): boolean} targetValueCondition - Filter fr gltige Ziel-Werte.
 * @param {boolean} [debug=false] - Debug-Flag fr Konsolenausgaben.
 */
export function dependenciesValuesXvalues(
    triggerName,
    condition,
    targetOptionName,
    targetValueCondition,
    debug = false
) {
    const log = (...args) => debug && console.log('[ValuesXvalues]', ...args);
    const warn = (...args) => debug && console.warn('[ValuesXvalues]', ...args);

    // 1. Autoselect des ersten sichtbaren Inputs mit Fokus-Effekt  bleibt unverndert
    const allGroupInputs = Array.from(
        document.querySelectorAll(
            '.values-group input[name="' + targetOptionName + '"]'
        )
    );

    // 2. Vor-caching aller Ziel-Inputs aus allGroupInputs + zugehrigem Label.btn
    const allTargetEntries = allGroupInputs
        .filter(input => targetValueCondition(input.value))
        .map(input => {
            const btn = input.closest('label.btn');
            if (!btn) {
                warn('Kein Container fr', input.value);
            }
            return { input, btn };
        });

    // 3. Warnung, wenn keine gltigen Ziel-Inputs gefunden werden
    if (allTargetEntries.length === 0) {
        warn('Keine gltigen Ziel-Inputs gefunden fr', targetOptionName);
        return;
    }

    // 4. Change-Handler
    function handleChange() {
        const value = this.value;
        const match = condition(value);
        log('Trigger', value, '-> condition', match);

        allTargetEntries.forEach(({ input, btn }) => {
            if (!btn) return;
            btn.classList.toggle('d-none', !match);
            if (!match) input.checked = false;
        });

        autoSelect(allGroupInputs);
    }

    // 5. Event-Delegation: nur ein Listener fr alle Trigger-Inputs
    document.body.addEventListener('change', event => {
        const tgt = event.target;
        if (tgt.name === triggerName) {
            handleChange.call(tgt, event);
        }
    });

    // 6. Initialer Lauf fr bereits gecheckte Trigger-Inputs
    // document
    //     .querySelectorAll(`input[name="${triggerName}"]:checked`)
    //     .forEach(input => handleChange.call(input));
}


/**
 * Dependencies: Values  Container
 * Steuerung eines ganzen Containers basierend auf dem Wert eines Trigger-Inputs.
 * @param {string} triggerName - Name der Trigger-Input-Gruppe.
 * @param {function(string): boolean} condition - Abhngigkeitsprfung fr Trigger-Wert.
 * @param {string} targetIdSuffix - Suffix fr die ID des Ziel-Containers (#option_{suffix}).
 * @param {boolean} [debug=false] - Debug-Flag fr Konsolenausgaben.
 */
export function dependenciesValuesXcontainer(
    triggerName,
    condition,
    targetIdSuffix,
    debug = false
) {
    const log = (...args) => debug && console.log('[ValuesXcontainer]', ...args);
    const warn = (...args) => debug && console.warn('[ValuesXcontainer]', ...args);

    log('Trigger name:', triggerName);

    // Ziel-Container ermitteln
    const container = document.getElementById(`option_${targetIdSuffix}`);
    if (!container) {
        warn('Container nicht gefunden:', `#option_${targetIdSuffix}`);
        return;
    }

    // Trigger-Handler
    const handleChange = function (event) {
        const match = condition(this.value);
        log('Trigger', this.value, '-> condition', match);
        log('Condition evaluated:', condition);

        if (match) {
            container.classList.remove('d-none');
            // Falls Number- oder Text-Input leer, setzen wir Default-Werte
            ['number', 'text'].forEach(type => {
                const input = container.querySelector(`input[type="${type}"]`);
                if (input && input.value.trim() === '') {
                    const fallback = input.getAttribute(type === 'number' ? 'min' : 'placeholder') || '';
                    input.value = fallback;
                    input.classList.replace('no-input', 'yes-input');
                    setTimeout(() => input.classList.add('focus', 'focus-ring', 'focus-ring-secondary'), 300);
                    setTimeout(() => input.classList.remove('focus', 'focus-ring', 'focus-ring-secondary'), 3300);
                }
            });

            // Autoselect innerhalb des Containers
            const allInputs = Array.from(container.querySelectorAll('input'));
            autoSelect(allInputs, () => applyFocusEffect(container, 'focus-ring-secondary'));
        } else {
            container.classList.add('d-none');

            // Reset von Number- und Text-Inputs
            container.querySelectorAll('input').forEach(input => {
                if (['number', 'text'].includes(input.type)) {
                    input.value = '';
                    input.classList.replace('yes-input', 'no-input');
                }
            });

            // Leeres Fallback-Input auswhlen, wenn vorhanden
            const empty = container.querySelector('input[value=""]');
            if (empty) {
                empty.checked = true;
                empty.dispatchEvent(new Event('change', { bubbles: true }));
            }
        }
    };

    // Event-Delegation
    document.body.addEventListener('change', event => {
        const tgt = event.target;
        if (tgt.matches(`input[name="${triggerName}"]`)) {
            handleChange.call(tgt, event);
        }
    });

    // Initialer Lauf
    document
        .querySelectorAll(`input[name="${triggerName}"]:checked`)
        .forEach(input => handleChange.call(input, null));
}