document.addEventListener("DOMContentLoaded", function () {
  // Alle relevanten Felder auswhlen
  const fields = document.querySelectorAll(
    'input.option-input[type="number"], input.option-input[type="text"], textarea.option-input'
  );

  // Zustzliche Selektoren fr "breite" und "ablage_breite"
  const breiteField = document.querySelector('input[name="breite"]');
  const ablageBreiteField = document.querySelector('input[name="ablage_breite"]');

  /**
   * Stellt sicher, dass es fr das bergebene Feld ein <div class="invalid-feedback"> gibt.
   * Falls nicht vorhanden, wird es erzeugt und direkt nach der .form-floating-Div eingefgt
   * (oder ins Elternelement, falls keine .form-floating-Klasse vorhanden).
   */
  function ensureInvalidContainer(field) {
    let invalidFeedback;
    // Nchstes bergeordnetes .form-floating ermitteln (falls vorhanden)
    const formFloating = field.closest(".form-floating");

    if (formFloating) {
      // Prfen, ob das unmittelbare nchste Geschwister-Element eine invalid-feedback-Div ist
      const nextSibling = formFloating.nextElementSibling;
      if (nextSibling && nextSibling.classList.contains("invalid-feedback")) {
        // Falls ja, einfach wiederverwenden
        invalidFeedback = nextSibling;
      } else {
        // Falls nicht vorhanden, neu anlegen
        invalidFeedback = document.createElement("div");
        invalidFeedback.classList.add("invalid-feedback");
        // Erst einmal unsichtbar (wird erst bei Fehlermeldung eingeblendet)
        invalidFeedback.style.display = "none";
        // Direkt nach der .form-floating-Div einfgen
        formFloating.insertAdjacentElement("afterend", invalidFeedback);
      }
    } else {
      // Kein form-floating: Standard-Ansatz, wir hngen invalid-feedback ins Elternelement des Felds
      invalidFeedback = field.parentNode.querySelector(".invalid-feedback");
      if (!invalidFeedback) {
        invalidFeedback = document.createElement("div");
        invalidFeedback.classList.add("invalid-feedback");
        // Erst einmal unsichtbar
        invalidFeedback.style.display = "none";
        field.parentNode.appendChild(invalidFeedback);
      }
    }

    return invalidFeedback;
  }

  /**
   * Schreibt die bergebene Fehlermeldung in das .invalid-feedback-Element
   * (wird bei Bedarf vorher angelegt). Falls message leer ist, wird es ausgeblendet.
   */
  function setInvalidFeedback(field, message) {
    const feedbackElement = ensureInvalidContainer(field);
    feedbackElement.textContent = message;

    // Wenn es eine Fehlermeldung gibt -> anzeigen, sonst ausblenden
    if (message) {
      feedbackElement.style.display = "block";
    } else {
      feedbackElement.style.display = "none";
    }
  }

  /**
   * Validiert das Feld anhand von:
   *   - required (darf nicht leer sein)
   *   - min/max (nur bei type="number", falls ein Wert eingetragen ist)
   *   - und setzt entsprechend die Bootstrap-Klassen 'is-valid' / 'is-invalid'
   *   - schreibt ggf. eine Fehlermeldung via setInvalidFeedback
   */
  function validateField(field) {
    let messages = [];

    // 1) Required-Check
    if (field.hasAttribute("required") && field.value.trim() === "") {
      messages.push("Dieses Feld ist erforderlich.");
    }

    // 2) Min-/Max-Check
    if (field.type === "number" && field.value.trim() !== "") {
      const val = parseFloat(field.value);
      const minAttr = field.getAttribute("min");
      const maxAttr = field.getAttribute("max");

      if (minAttr !== null && val < parseFloat(minAttr)) {
        messages.push(`Der Wert muss mindestens ${minAttr} sein.`);
      }
      if (maxAttr !== null && val > parseFloat(maxAttr)) {
        messages.push(`Der Wert darf hchstens ${maxAttr} sein.`);
      }
    }

    // 3) Relational-Check fr "obere_breite"
    if (field.name === "obere_breite") {
      const obereBreite = parseFloat(field.value) || 0;
      const breite = parseFloat(document.querySelector('input[name="breite"]').value) || 0;
      if (obereBreite > breite) {
        messages.push('Die "obere Breite" darf nicht grer sein als die Breite.');
      }
    }
    if (field.name === "kuerze_hoehe") {
      const kuerzeHoehe = parseFloat(field.value) || 0;
      const hoehe = parseFloat(document.querySelector('input[name="hoehe"]').value) || 0;
      if (kuerzeHoehe > hoehe) {
        messages.push('Die "krzere Hhe" darf nicht grer sein als die Haupthhe.');
      }
    }

    // 4) Relationale Validierung fr "ablage_breite"
    // if (field.name === "ablage_breite") {
    //   const ablageBreite = parseFloat(field.value) || 0;
    //   const breite = parseFloat(breiteField ? breiteField.value : 0) || 0;
    //   if (ablageBreite > breite) {
    //     messages.push("Die Ablage Breite darf nicht grer sein als die Breite.");
    //   }
    // }

    // 5) Anhand der gesammelten messages setzen wir is-invalid / invalid-feedback
    if (messages.length > 0) {
      field.classList.add("is-invalid");
      setInvalidFeedback(field, messages.join(" "));
    } else {
      field.classList.remove("is-invalid");
      setInvalidFeedback(field, "");
    }
  }

  /**
   * Setzt je nach Zustand die Klassen:
   *   - no-input (leer + nicht fokussiert)
   *   - yes-input (befllt + nicht fokussiert)
   *   - on-input (fokussiert)
   * und ruft danach die Feldvalidierung auf.
   */
  function updateClasses(field) {
    // Fokus?
    if (document.activeElement === field) {
      field.classList.add("on-input");
      // field.classList.remove("no-input", "yes-input");
    } else {
      field.classList.remove("on-input");

      if (field.value.trim() === "") {
        field.classList.add("no-input");
        field.classList.remove("yes-input");
      } else {
        field.classList.add("yes-input");
        field.classList.remove("no-input");
      }
    }

    // Anschlieend Validierung ausfhren
    validateField(field);
  }

  // Neue Funktion: Aktualisiert das max-Attribut von "ablage_breite" basierend auf "breite"
  function updateAblageBreiteMax() {
    if (breiteField && ablageBreiteField) {
      const breiteValue = parseFloat(breiteField.value) || 0;
      ablageBreiteField.setAttribute("max", breiteValue);
      // Falls der aktuelle Wert von ablage_breite grer ist als die Breite, gleich korrigieren
      const ablageBreiteValue = parseFloat(ablageBreiteField.value) || 0;
      if (ablageBreiteValue > breiteValue) {
        ablageBreiteField.value = breiteValue;
      }
      updateClasses(ablageBreiteField);
    }
  }

  // Initialer Durchlauf + Event-Listener
  fields.forEach(function (field) {
    // Beim Laden / Focus bleibt unverndert 
    field.addEventListener("focus", function () {
      updateClasses(field);
    });

    //  Neu: input-Handler mit is-valid-Logik
    field.addEventListener("input", function () {
      updateClasses(field);

      if (field.name === "breite") {
        updateAblageBreiteMax();
      }

      // is-valid nur whrend Tippen und ohne Fehler
      if (document.activeElement === field && !field.classList.contains("is-invalid")) {
        field.classList.add("is-valid");
      } else {
        field.classList.remove("is-valid");
      }
    });

    //  Neu: blur-Handler entfernt is-valid
    field.addEventListener("blur", function () {
      // 1) Optional: autoRoundToRange(field);
      // 2) Validierung
      updateClasses(field);

      // is-valid wird beim Verlassen wieder entfernt
      field.classList.remove("is-valid");
    });
  });


  // Beim Laden der Seite das max-Attribut von "ablage_breite" setzen, falls "breite" bereits einen Wert hat
  // updateAblageBreiteMax();
});
