// Hilfsfunktion: aktualisiert das P-Element hinter dem Select
function updatePriceLabel(select) {
  const opt = select.options[select.selectedIndex];
  const p = select.nextElementSibling;
  if (!p || p.tagName !== 'P') return;

  // Spans im P-Element
  const nameSpan = p.querySelector('.sub_name');
  const labelSpan = p.querySelector('.sub_label');
  const priceSpan = p.querySelector('.sub_price');

  // Name-Span nur fllen, wenn name mit "px" beginnt
  if (nameSpan) {
    nameSpan.textContent = select.name && select.name.startsWith('pxbh')
      ? 'Mae'
      : select.name && select.name.startsWith('pxd')
        ? 'Durchmesser'
        : select.name && select.name.startsWith('pxt')
          ? 'Tiefe'
          : select.name && select.name.startsWith('pxs')
            ? 'Schnittkante'
            : select.name && select.name.startsWith('pxr')
              ? 'Rahmenbreite'
              : '';
  }

  // Label und Preis fllen
  if (labelSpan) labelSpan.textContent = opt.dataset.label || '';
  if (priceSpan) priceSpan.textContent = opt.dataset.price || '';
}

// Initiales Befllen aller passenden Selects
document.querySelectorAll('select.option-pricematrix, select.option-price')
  .forEach(sel => updatePriceLabel(sel));

// Auf nderungen lauschen (Event-Delegation)
document.addEventListener('change', event => {
  const sel = event.target;
  if (sel.matches('select.option-pricematrix, select.option-price')) {
    updatePriceLabel(sel);
  }
});