import {
    durchmesserInput,
    breiteInput,
    hoeheInput,
    breiteHoheInput,
    isLORU,
    isLOR,
    isLR,
    isOU,
    isO
} from './../variables.js';

document.addEventListener("DOMContentLoaded", () => {
    // Hauptbeleuchtung
    const lichtValueHue = document.querySelector("input[name='lichtfarbe'][value='philips_hue']");
    const lichtInputHue = document.querySelectorAll("input[name='lichtfarbe_philips_hue']");
    const lichtSelectPlus = document.querySelector("select[name='lichtfarbe_philips_hue_lightstrip_aufpreis']");
    const lichtSelectGradient = document.querySelector("select[name='lichtfarbe_philips_hue_gradient_aufpreis']");

    // Ambientelicht
    const ambientValueHue = document.querySelector("input[value='philips_hue'][name='ambientelicht_lichtfarbe']");
    const ambientInputHue = document.querySelectorAll("input[name='ambientelicht_philips_hue']");
    const ambientSelectPlus = document.querySelector("select[name='ambientelicht_philips_hue_lightstrip_aufpreis']");
    const ambientSelectGradient = document.querySelector("select[name='ambientelicht_philips_hue_gradient_aufpreis']");

    // Hilfsfunktionen
    const parseInputValue = (input) => {
        const parsed = parseFloat(input);
        return isNaN(parsed) ? 0 : parsed / 1000; // mm in m
    };

    // Allgemeine Funktion zum Setzen des Preises
    const setRadioPrice = (radioElems, radioValue, price) => {
        radioElems.forEach(radio => {
            if (radio.value === radioValue) {
                radio.dataset.price = price;
                radio.setAttribute('data-price', price);
            }
        });
    };

    // Allgemeine Funktion zur Preisanzeige
    const updatePriceDisplay = (radioElems, radioValue, price) => {
        const radio = Array.from(radioElems).find(r => r.value === radioValue);
        if (!radio) return;
        const label = document.querySelector(`label.btn[for="${radio.id}"]`);
        const priceContainer = label?.querySelector('.col-auto:last-child');
        if (priceContainer) {
            priceContainer.textContent = price ? `(+${price} )` : '';
        }
    };

    // Aktualisiert die Basis-Preisanzeige fr Philips Hue
    const updateBasePriceDisplay = (radioElem, price) => {
        if (!radioElem) return;
        const label = document.querySelector(`label.btn[for="${radioElem.id}"]`);
        const priceContainer = label?.querySelector('.col-auto:last-child');
        if (priceContainer) {
            priceContainer.textContent = price ? `(ab ${price} )` : '';
        }
    };

    // Umfang berechnen
    const calcUmfang = (type = 'ambientelicht') => {
        if (durchmesserInput?.value) {
            const d = parseInputValue(durchmesserInput.value);
            return Math.ceil(Math.PI * d * 100) / 100;
        }
        if (breiteHoheInput?.value) {
            const s = parseInputValue(breiteHoheInput.value);
            return Math.ceil(4 * s * 100) / 100;
        }
        if (breiteInput?.value || hoeheInput?.value) {
            const b = parseInputValue(breiteInput.value);
            const h = parseInputValue(hoeheInput.value);

            if (type === 'ambientelicht') {
                const selectedOption = document.querySelector("input[name='ambientelicht']:checked")?.value;
                switch (selectedOption) {
                    case "waschbeckenbeleuchtung":
                    case "deckenbeleuchtung":
                    case "unten":
                    case "oben":
                        return Math.ceil(b * 100) / 100;
                    case "seitenbeleuchtung":
                        return Math.ceil(2 * h * 100) / 100;
                    case "wasch_und_deckenbeleuchtung":
                    case "oben_und_unten":
                        return Math.ceil(2 * b * 100) / 100;
                    case "wasch_und_seitenbeleuchtung":
                    case "decken_und_seitenbeleuchtung":
                        return Math.ceil((b + 2 * h) * 100) / 100;
                    case "rundherumbeleuchtung":
                        return Math.ceil(2 * (b + h) * 100) / 100;
                    default:
                        return 0;
                }
            } else if (type === 'lichtfarbe') {
                if (isLORU) return Math.ceil(2 * (b + h) * 100) / 100;
                if (isLOR) return Math.ceil((b + 2 * h) * 100) / 100;
                if (isLR) return Math.ceil(2 * h * 100) / 100;
                if (isOU) return Math.ceil(2 * b * 100) / 100;
                if (isO) return Math.ceil(b * 100) / 100;
                return 0;
            }
        }
        return 0;
    };

    // Kategorie ermitteln
    const mapKategorie = (u) => {
        const kategorien = { 2: "bis_2m", 3: "bis_3m", 4: "bis_4m", 5: "bis_5m", 6: "bis_6m", 8: "bis_8m", 9: "bis_9m", 10: "bis_10m" };
        for (const [max, kategorie] of Object.entries(kategorien)) {
            if (u <= max) return kategorie;
        }
        return "";
    };

    // Hauptfunktion Hauptlicht
    const updateLichtHueType = () => {
        const u = calcUmfang('lichtfarbe');
        const val = mapKategorie(u);
        const selectedType = document.querySelector("input[name='lichtfarbe_philips_hue']:checked")?.value;

        const plusPrice = lichtSelectPlus?.querySelector(`option[value="${val}"]`)?.dataset.price || "";
        const gradientPrice = lichtSelectGradient?.querySelector(`option[value="${val}"]`)?.dataset.price || "";

        updateBasePriceDisplay(lichtValueHue, plusPrice);
        updatePriceDisplay(lichtInputHue, "lightstrip_plus", plusPrice);
        updatePriceDisplay(lichtInputHue, "lightstrip_gradient", gradientPrice);

        if (selectedType === "lightstrip_gradient") {
            if (lichtSelectGradient) lichtSelectGradient.value = val;
            if (lichtSelectPlus) lichtSelectPlus.value = "";
            setRadioPrice(lichtInputHue, "lightstrip_gradient", gradientPrice);
        } else if (selectedType === "lightstrip_plus") {
            if (lichtSelectPlus) lichtSelectPlus.value = val;
            if (lichtSelectGradient) lichtSelectGradient.value = "";
            setRadioPrice(lichtInputHue, "lightstrip_plus", plusPrice);
        }
    };

    // Hauptfunktion Ambientelicht
    const updateAmbientHueType = () => {
        const u = calcUmfang('ambientelicht');
        const val = mapKategorie(u);
        const selectedType = document.querySelector("input[name='ambientelicht_philips_hue']:checked")?.value;

        const plusPrice = ambientSelectPlus?.querySelector(`option[value="${val}"]`)?.dataset.price || "";
        const gradientPrice = ambientSelectGradient?.querySelector(`option[value="${val}"]`)?.dataset.price || "";

        updateBasePriceDisplay(ambientValueHue, plusPrice);
        updatePriceDisplay(ambientInputHue, "lightstrip_plus", plusPrice);
        updatePriceDisplay(ambientInputHue, "lightstrip_gradient", gradientPrice);

        if (selectedType === "lightstrip_gradient") {
            if (ambientSelectGradient) ambientSelectGradient.value = val;
            if (ambientSelectPlus) ambientSelectPlus.value = "";
            setRadioPrice(ambientInputHue, "lightstrip_gradient", gradientPrice);
        } else if (selectedType === "lightstrip_plus") {
            if (ambientSelectPlus) ambientSelectPlus.value = val;
            if (ambientSelectGradient) ambientSelectGradient.value = "";
            setRadioPrice(ambientInputHue, "lightstrip_plus", plusPrice);
        }
    };

    // Event-Listener
    const inputs = [durchmesserInput, breiteHoheInput, breiteInput, hoeheInput].filter(Boolean);
    inputs.forEach(input => {
        input.addEventListener("input", () => {
            const value = parseFloat(input.value);
            const min = parseFloat(input.min);
            const max = parseFloat(input.max);

            if (!isNaN(value) && value >= min && value <= max) {
                updateLichtHueType();
                updateAmbientHueType();
            }
        });
    });

    if (lichtInputHue.length) {
        lichtInputHue.forEach(radio => radio.addEventListener("change", updateLichtHueType));
        requestAnimationFrame(updateLichtHueType);
    }

    if (ambientInputHue.length) {
        ambientInputHue.forEach(radio => radio.addEventListener("change", updateAmbientHueType));
        requestAnimationFrame(updateAmbientHueType);
    }
});