// Product
export const product = document.querySelector('.product');

export const isLED = product?.querySelectorAll('[class*="mit-beleuchtung"]').length > 0;
export const isLORU = product?.querySelectorAll('.product_attr-beleuchtung_rundherum').length > 0;
export const isLOR = product?.querySelectorAll('.product_attr-beleuchtung_links-oben-rechts').length > 0;
export const isLR = product?.querySelectorAll('.product_attr-beleuchtung_links-rechts').length > 0;
export const isOU = product?.querySelectorAll('.product_attr-beleuchtung_oben-unten').length > 0;
export const isO = product?.querySelectorAll('.product_attr-beleuchtung_oben').length > 0;

export const Badspiegel = document.querySelectorAll('.product_cat-badspiegel');
export const isBadspiegel = Badspiegel?.length > 0;

export const Spiegelschrank = document.querySelectorAll('.product_cat-spiegelschraenke');
export const isSpiegelschrank = Spiegelschrank?.length > 0;

export const Rund = document.querySelectorAll('.product_attr-form_rund');
export const isRund = Rund?.length > 0;

export const SK = document.querySelectorAll('.product_attr-form_halbmond');
export const SK1 = document.querySelectorAll('.product_attr-schnittkanten_1');
export const SK2 = document.querySelectorAll('.product_attr-schnittkanten_2');
export const isSK = SK?.length > 0;
export const isSK1 = SK1?.length > 0;
export const isSK2 = SK2?.length > 0;

