  document.querySelector('.option-input[name="breite_hochschrank"]') ||
  document.querySelector('.option-input[name="breite_kristall"]');
export const hoeheInput =
  document.querySelector('.option-input[name="hoehe"]') ||
  document.querySelector('.option-input[name="hoehe_tv"]') ||
  document.querySelector('.option-input[name="hoehe_rahmen"]') ||
  document.querySelector('.option-input[name="hoehe_dachschraege"]') ||
  document.querySelector('.option-input[name="hoehe_hochschrank"]') ||
  document.querySelector('.option-input[name="hoehe_kristall"]');
export const breiteHoheInput = document.querySelector('.option-input[name="breite_hoehe"]');
