import {
    isSK,
    isSK1,
    durchmesserInput
} from '../variables';

document.addEventListener('DOMContentLoaded', () => {
    // ---------------------------
    // 0. Prfen ob SK
    // ---------------------------
    if (!isSK || !isSK1) {
        // console.warn('[SK1] Nicht die richtige Produktseite.');
        return;
    }

    // ---------------------------
    // 1. DOM-Referenzen ermitteln
    // ---------------------------
    // const durchmesserInput = document.querySelector('.option-input[name="durchmesser"]');
    // Die "hoehe_schnittkante" kann auch "breite_schnittkante" heien (oder umgekehrt):
    const hoeheInput =
        document.querySelector('.option-input[name="hoehe_schnittkante"]') ||
        document.querySelector('.option-input[name="breite_schnittkante"]');
    const schnittkanteInput = document.querySelector('.option-input[name="schnittkante"]');

    // Alle gefundenen Inputs in ein Array (ohne null)
    const inputs = [durchmesserInput, hoeheInput, schnittkanteInput].filter(Boolean);

    // Abbruch, wenn gar keine Inputs gefunden wurden
    if (inputs.length === 0) {
        console.warn('[SK1] Keine relevanten Eingabefelder gefunden.');
        return;
    }

    // ----------------------------------
    // 2. Event-Listener nur fr vorhandene Inputs
    // ----------------------------------
    inputs.forEach((el) => {
        el.addEventListener('input', handleInputChange);
    });

    /**
     * Zentraler Input-Change-Handler
     */
    function handleInputChange(e) {
        const changed = e.target.name; // "durchmesser", "hoehe_schnittkante" oder "schnittkante"

        // Aktuelle Werte auslesen
        const dVal = parseFloat(durchmesserInput?.value);  // Durchmesser
        const hVal = parseFloat(hoeheInput?.value);          // Hhe
        const tVal = parseFloat(schnittkanteInput?.value);   // Schnittkante

        // Abbruch, falls alle drei NaN sind (keine gltige Zahl)
        if (Number.isNaN(dVal) && Number.isNaN(hVal) && Number.isNaN(tVal)) {
            return;
        }

        switch (changed) {
            case 'durchmesser':
                // Durchmesser gendert => Schnittkante (t) bleibt, Hhe (h) wird neu berechnet
                if (!Number.isNaN(dVal) && !Number.isNaN(tVal)) {
                    updateHeightFromDT(dVal, tVal);
                }
                break;

            case 'hoehe_schnittkante':
            case 'breite_schnittkante':
                // Hhe (bzw. Breite) gendert => Schnittkante (t) bleibt, Durchmesser (d) wird neu berechnet
                if (!Number.isNaN(hVal) && !Number.isNaN(tVal)) {
                    updateDiameterFromHT(hVal, tVal);
                }
                break;

            case 'schnittkante':
                // Schnittkante gendert => Durchmesser (d) bleibt, Hhe (h) wird neu berechnet
                if (!Number.isNaN(dVal) && !Number.isNaN(tVal)) {
                    updateHeightFromDT(dVal, tVal);
                }
                break;
        }
    }

    /**
     * Update der "Hhe" gegeben Durchmesser (d) und Schnittkante (t)
     */
    function updateHeightFromDT(d, t) {
        const newH = calcHeightGivenDT(d, t);
        if (isFinite(newH) && hoeheInput) {
            hoeheInput.value = round(newH);
        }
    }

    /**
     * Update des "Durchmessers" gegeben Hhe (h) und Schnittkante (t)
     */
    function updateDiameterFromHT(h, t) {
        const newD = calcDiameterGivenHT(h, t);
        if (isFinite(newD) && durchmesserInput) {
            durchmesserInput.value = round(newD);
        }
    }

    /**
     * Berechnung: Segmenthhe aus Durchmesser und Schnittkante
     *
     * h = 2*r - (r - sqrt(r^2 - (t^2)/4))
     */
    function calcHeightGivenDT(d, t) {
        const r = d / 2;
        const disc = r * r - (t * t) / 4; // r^2 - (t^2 / 4)
        if (disc < 0) return NaN;

        const i = r - Math.sqrt(disc);
        // h = 2*r - i
        return 2 * r - i;
    }

    /**
     * Berechnung: Durchmesser aus Hhe und Schnittkante
     *
     * d = h + (t^2)/(4h)
     */
    function calcDiameterGivenHT(h, t) {
        if (h === 0) return NaN;
        const numerator = (t * t) / 4 + h * h;
        return numerator / h;
    }

    /**
     * Runden auf ganze Zahlen (keine Nachkommastellen)
     */
    function round(val) {
        return Math.round(val);
    }
});
