/**
 * Funktion zum Sammeln der Konfigurationsdaten
 */
function gatherConfigData() {
    let configData = {};

    // Radios
    $('input.option-radio:checked').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'radio' };
    });

    // Checkbox
    $('input.option-check:checked').each(function () {
        let name = $(this).attr('name');
        if (!configData[name]) {
            configData[name] = { value: [], type: 'checkbox' };
        }
        configData[name].value.push($(this).val());
    });

    // Textfelder
    $('input.option-input').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'input' };
    });

    // Textareas
    $('textarea.option-textarea').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'textarea' };
    });

    // Select-Felder
    $('select.option-select').each(function () {
        let name = $(this).attr('name');
        let val = $(this).val();
        configData[name] = { value: val, type: 'select' };
    });

    return configData;
}

/**
 * Zeigt ein Erfolgs-Modal mit dem generierten Code und Tooltip an.
 *
 * @param {String} msg     - Besttigungstext.
 * @param {String} code    - Generierter Konfigurationscode.
 * @param {String} tooltip - Tooltip-Text fr das Kopier-Button.
 */
function showSuccessModal(msg, code, tooltip) {
    const modalBody = `
        <p>${msg}</p>
        <button type="button"
                class="btn btn-outline-dark"
                data-copy="clipboard"
                data-voucher="${code}"
                data-bs-toggle="tooltip"
                title="${tooltip}">
            ${code} <i class="fa-sharp fa-light fa-copy" aria-hidden="true"></i>
        </button>
    `;

    createModal({
        title: 'Erfolg',
        body: modalBody,
        size: 'md',
        footer: { cancel: 'Schlieen' }
    });

    // Tooltip initialisieren
    $('[data-bs-toggle="tooltip"]').tooltip();
}

/**
 * Click-Handler zum Speichern der Konfiguration.
 */
$('#product-configurator-configcode-save').on('click', function () {
    const $button = $(this);

    // Button deaktivieren und Spinner anzeigen
    $button.prop('disabled', true)
        .append('<span class="spinner-border spinner-border-sm ms-2" role="status" aria-hidden="true" data-id="saveSpinner"></span>');

    // Konfigurationsdaten sammeln
    const configData = gatherConfigData();

    // AJAX-Request an das WordPress-Backend
    $.ajax({
        url: myAjaxData.ajaxUrl,
        method: 'POST',
        data: {
            action: 'save_config',
            security: myAjaxData.nonce,
            product_id: myAjaxData.productId,
            config_data: configData
        },
        success(response) {
            if (response.success) {
                const { generated_code: code, msg, tooltip } = response.data;
                showSuccessModal(msg, code, tooltip);
            }
        },
        complete() {
            // Spinner entfernen und Button reaktivieren
            $button.find('[data-id="saveSpinner"]').remove();
            $button.prop('disabled', false);
        }
    });
});
