export function calcPrice2x100(inputElement1, inputElement2, pricematrixSelect, pricematrixCache, lastValueCache) {
  const inputValue1 = parseInt(inputElement1.value, 10);
  const inputValue2 = parseInt(inputElement2.value, 10);
  if (isNaN(inputValue1) || isNaN(inputValue2)) return;

  // Schritt 1: Auf 100er runden
  let roundedValue1 = Math.ceil(inputValue1 / 100) * 100;
  let roundedValue2 = Math.ceil(inputValue2 / 100) * 100;
  let potentialValue = `${roundedValue1}x${roundedValue2}`;

  // Prfen, ob wir bereits das gleiche Ergebnis hatten
  if (lastValueCache.lastRoundedValue === potentialValue) {
    return;
  }

  // Passende Option im Select suchen
  let optionToSelect = Array.from(pricematrixSelect.options).find(
    (opt) => opt.value === potentialValue
  );

  // Schritt 2: Falls nicht vorhanden, auf 50er runden
  if (!optionToSelect) {
    roundedValue1 = Math.ceil(inputValue1 / 50) * 50;
    roundedValue2 = Math.ceil(inputValue2 / 50) * 50;
    potentialValue = `${roundedValue1}x${roundedValue2}`;

    optionToSelect = Array.from(pricematrixSelect.options).find(
      (opt) => opt.value === potentialValue
    );

    // Wenn wir hier immer noch keine Option finden, abbrechen
    if (!optionToSelect) {
      return;
    }
  }

  // Cache aktualisieren
  lastValueCache.lastRoundedValue = potentialValue;

  // Select setzen
  pricematrixSelect.value = optionToSelect.value;
  
  // data-price mit dem Wert aus dem gefundenen <option> setzen (falls relevant)
  pricematrixSelect.setAttribute("data-price", optionToSelect.getAttribute("data-price"));

  // Ggf. ein "change"-Event auslsen
  const event = new Event('change', { bubbles: true });
  pricematrixSelect.dispatchEvent(event);
}

