import {
    isSK,
    isSK2,
} from '../variables';

document.addEventListener('DOMContentLoaded', () => {
    // ---------------------------
    // 0. Prfen ob SK
    // ---------------------------
    if (!isSK || !isSK2) {
        return;
    }

    // ---------------------------
    // 1. Parameter (Konstanten)
    // ---------------------------
    const a = 1.077;
    const b = 0.08;

    // ---------------------------
    // 2. Hilfsfunktionen
    // ---------------------------
    /**
     * Berechnet den Durchmesser aus Breite/Hhe
     * @param {number} bh
     * @returns {number}
     */
    function computeDiameterFromBH(bh) {
        return a * bh + b;
    }

    /**
     * Berechnet Breite/Hhe aus dem Durchmesser
     * @param {number} d
     * @returns {number}
     */
    function computeBHFromDiameter(d) {
        return (d - b) / a;
    }

    // ---------------------------
    // 3. DOM-Referenzen
    // ---------------------------
    const bhInput = document.querySelector('.option-input[name="breite_hoehe"]');
    const dInput = document.querySelector('.option-input[name="durchmesser"]');

    if (!bhInput || !dInput) {
        console.warn("Eines oder beide Eingabefelder wurden im DOM nicht gefunden.");
        return;
    }

    // ---------------------------
    // 4. Debounce-Helfer
    // ---------------------------
    /**
     * Fhrt die bergebene Funktion erst nach einer Verzgerung aus,
     * sofern in der Zwischenzeit keine neue Eingabe erfolgt.
     * @param {Function} func
     * @param {number} delay - Verzgerung in Millisekunden
     * @returns {Function}
     */
    function debounce(func, delay) {
        let timeoutId;
        return function (...args) {
            if (timeoutId) clearTimeout(timeoutId);
            timeoutId = setTimeout(() => {
                func.apply(this, args);
            }, delay);
        };
    }

    // ---------------------------
    // 5. Umrechnungs-Funktionen (ohne Event-Objekt)
    // ---------------------------
    function updateDiameterFromBH() {
        const valBH = parseFloat(bhInput.value);
        if (isNaN(valBH)) return; // Bei ungltiger Eingabe nichts tun

        const newD = Math.round(computeDiameterFromBH(valBH));
        if (String(newD) !== dInput.value) {
            dInput.value = newD;
            // Optional: Event dispatchen, falls andere Listener reagieren sollen
            dInput.dispatchEvent(new Event('input', { bubbles: true }));
        }
    }

    function updateBHFromDiameter() {
        const valD = parseFloat(dInput.value);
        if (isNaN(valD)) return;

        const newBH = Math.round(computeBHFromDiameter(valD));
        if (String(newBH) !== bhInput.value) {
            bhInput.value = newBH;
            bhInput.dispatchEvent(new Event('input', { bubbles: true }));
        }
    }

    // ---------------------------
    // 6. Debounced Event-Handler
    // ---------------------------
    const debouncedBHHandler = debounce(updateDiameterFromBH, 500);
    const debouncedDHandler = debounce(updateBHFromDiameter, 500);

    // ---------------------------
    // 7. Event-Listener anhngen
    // ---------------------------
    // Bei "input" wird erst nach 500ms ohne weitere Eingaben umgerechnet
    bhInput.addEventListener('input', debouncedBHHandler);
    dInput.addEventListener('input', debouncedDHandler);

    // Zustzlich: Bei "blur" (Verlassen des Felds) wird sofort umgerechnet
    bhInput.addEventListener('blur', updateDiameterFromBH);
    dInput.addEventListener('blur', updateBHFromDiameter);

    // ---------------------------
    // Optional: Initiale Synchronisierung falls im DOM bereits Werte stehen
    // updateDiameterFromBH();
    // updateBHFromDiameter();
});
