/**
 * Applies configuration data to the form.
 * Returns an array of fields that could not be mapped (notFound).
 */
function applyConfigData(configData) {
    const notFound = [];

    // Reset all relevant fields before applying new values
    $('input.option-radio, input.option-check').prop('checked', false);
    $('input.option-input, textarea.option-textarea').val('');
    $('select.option-select').prop('selectedIndex', 0);

    // Apply each field's value
    $.each(configData, (fieldName, fieldObj) => {
        const { type: fieldType, value: fieldValue } = fieldObj;

        switch (fieldType) {
            case 'radio': {
                const $radio = $(`input[type="radio"][name="${fieldName}"][value="${fieldValue}"]`);
                if ($radio.length) {
                    $radio.prop('checked', true);
                    $radio[0].dispatchEvent(new Event('change', { bubbles: true }));
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            case 'input': {
                const $input = $(`input[name="${fieldName}"]`);
                if ($input.length) {
                    $input.val(fieldValue);
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            case 'select': {
                const $select = $(`select[name="${fieldName}"]`);
                if ($select.length) {
                    $select.val(fieldValue);
                } else {
                    notFound.push({ fieldName, fieldType, fieldValue });
                }
                break;
            }

            default: {
                console.warn('Unknown fieldType:', fieldType, fieldName, fieldValue);
                notFound.push({ fieldName, fieldType: 'unknown', fieldValue });
                break;
            }
        }
    });

    return notFound;
}

/**
 * Displays a validation modal when the loaded product ID does not match the current product ID.
 * The callback (onConfirm) is invoked if the user chooses to continue loading.
 *
 * @param {Function} onConfirm - A callback function to be executed if the user clicks "Ja, trotzdem laden".
 */
function showValidationModal(onConfirm) {
    const modalBody = `
        <div class="alert alert-warning d-flex align-items-center mb-0" role="alert">
            <i class="fa-sharp fa-light fa-exclamation-triangle me-3"></i>
            <div>Der geladene Code gehrt zu einem anderen Produkt. Mchtest du die Konfiguration trotzdem laden?</div>
        </div>
    `;
    createModal({
        title: 'Abweichendes Produkt',
        body: modalBody,
        footer: { cancel: 'Abbrechen', confirm: 'Ja, trotzdem laden' },
        onConfirm: onConfirm,
        size: 'md'
    });
}

/**
 * Displays the final success modal.
 * If forcedLoad === true, we also indicate that the product was different.
 */
function showSuccessModal(configData, successMsg, forcedLoad) {
    applyConfigData(configData);

    let modalBody = '';
    if (forcedLoad) {
        modalBody += `
            <div class="alert alert-info d-flex align-items-center mb-0" role="alert">
                <i class="fa-sharp fa-light fa-check me-3"></i>
                <div>Du hast die Konfiguration eines anderen Produkts geladen.</div>
            </div>
        `;
    } else {
        modalBody += `
            <div class="alert alert-success d-flex align-items-center mb-0" role="alert">
                <i class="fa-sharp fa-light fa-check me-3"></i>
                <div>${successMsg}</div>
            </div>
        `;
    }

    createModal({
        title: 'Konfiguration angewendet',
        body: modalBody,
        size: 'md'
    });
}

/**
 * Displays an error modal with a Bootstrap alert inside it.
 *
 * @param {String} errorMsg - The error message to display.
 * @param {String} [customTitle='Fehler'] - Optional custom title for the modal.
 */
function showErrorModal(errorMsg, customTitle = 'Fehler') {
    const modalBody = `
        <div class="alert alert-danger d-flex align-items-center mb-0" role="alert">
            <i class="fa-sharp fa-light fa-exclamation-triangle me-3"></i>
            <div>${errorMsg}</div>
        </div>
    `;
    createModal({
        title: customTitle,
        body: modalBody,
        size: 'md'
    });
}

// Trigger load on Enter key press
$('#product-configurator-configcode-input').on('keydown', function (event) {
    if (event.key === 'Enter' || event.keyCode === 13) {
        event.preventDefault();
        $('#product-configurator-configcode-load').trigger('click');
    }
});

// Click handler for loading a configuration via config code
$('#product-configurator-configcode-load').on('click', function () {
    const $button = $(this);
    const code = $('#product-configurator-configcode-input').val().trim();

    $button.append(`
        <span class="spinner-border spinner-border-sm ms-2" role="status" aria-hidden="true" id="loadSpinner"></span>
    `);
    $button.prop('disabled', true);

    $.ajax({
        url: myAjaxData.ajaxUrl,
        method: 'POST',
        data: {
            action: 'load_config',
            security: myAjaxData.nonce,
            config_code: code
        },
        success(response) {
            if (response.success) {
                const { msg, product_id, config_data } = response.data;
                const remoteProductId = parseInt(product_id, 10) || null;
                const currentProductId = myAjaxData.productId ? parseInt(myAjaxData.productId, 10) : null;
                doApply(config_data, msg, remoteProductId, currentProductId);
            } else {
                const errorMsg = response.data?.msg || 'Unbekannter Fehler';
                showErrorModal(errorMsg);
            }
        },
        error(xhr) {
            const errorMsg = xhr.responseJSON?.data?.msg || xhr.statusText;
            showErrorModal(errorMsg, 'AJAX Fehler');
        },
        complete() {
            $('#loadSpinner').remove();
            $button.prop('disabled', false);
        }
    });
});

/**
 * Applies the configuration (and handles mismatch warnings).
 */
function doApply(configData, successMsg, remoteProductId, currentProductId) {
    if (currentProductId && remoteProductId && remoteProductId !== currentProductId) {
        showValidationModal(() => showSuccessModal(configData, successMsg, true));
    } else {
        showSuccessModal(configData, successMsg, false);
    }
}
